import React, { Fragment } from 'react';
import Title from '../Title';
import { HashRouter as Router, NavLink, Switch, Route, useParams, useRouteMatch, useLocation } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import logo from '../../logo.svg';
import AppCss from './AppCss.module.css';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Content from '../Content';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhone, faMapMarker, faEnvelope } from '@fortawesome/free-solid-svg-icons';
import FormHandling from '../FormHandling';

function useQuery() {
    return new URLSearchParams(useLocation().search)
}

const UserInformation = () => {
    const { userId } = useParams();
    const QueryParams = useQuery();
    const query = [...QueryParams.entries()];
    return (
        <div>
            <Content>Users</Content>
            <div className="my-4">
                {
                    userId === undefined
                        ? query.map((val, index) => {
                            var string = val[0];
                            return <li key={index}> {string[0].toUpperCase() + string.slice(1)} : {val[1]}  </li>
                        }
                        )
                        : "User Id is " + userId
                }
            </div>
        </div>
    )
}

const PageNotFound = () => {
    const { url } = useRouteMatch();
    return (
        <div className={"my-4"}>
            <h6>{`${url}`.slice(1)} page is not found !!</h6>
        </div>
    )
}

const App = () => {
    return (
        <Fragment>
            <Title />
            <Router>
                <Navbar collapseOnSelect expand="md" bg="dark" variant="dark">
                    <Navbar.Brand className="mx-5">
                        <img src={logo} width="30px" height="30px" className="d-inline-block align-top" alt="React" />
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className={AppCss.navbarLink}>
                            <NavLink exact to="/" activeClassName={AppCss.active}>Home</NavLink>
                            <NavLink to="/users" activeClassName={AppCss.active}>Users</NavLink>
                            <NavLink to="/courses" activeClassName={AppCss.active}>Courses</NavLink>
                            <NavLink to="/form" activeClassName={AppCss.active}>Form</NavLink>
                            <NavLink to="/help" activeClassName={AppCss.active}>Help</NavLink>
                            <NavLink to="/about-us" activeClassName={AppCss.active}>About us</NavLink>
                            <NavLink to="/contact-us" activeClassName={AppCss.active}>Contact Us</NavLink>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>

                <Container className={AppCss.ContentSection}>
                    <Switch>
                        <Route path='/users/:userId?'><UserInformation /></Route>
                        <Route path="/courses"><Content>Courses</Content></Route>
                        <Route path="/form" component={FormHandling}></Route>
                        <Route path="/help"><Content>Help</Content></Route>
                        <Route path="/about-us"><Content>About us</Content></Route>
                        <Route path="/contact-us"><Content>Contact us</Content></Route>
                        <Route path="/frequently-asked-questions"><Content>Frequently Asked Questions</Content></Route>
                        <Route path="/privacy-policy"><Content>Privacy Policy</Content></Route>
                        <Route path="/resources"><Content>Resources</Content></Route>
                        <Route exact path="/"><Content>Home</Content></Route>
                        <Route exact path="*"><PageNotFound /></Route>
                    </Switch>
                </Container>

                <footer className={AppCss.Footer}>
                    <Container fluid>
                        <Row>
                            <Col md="6" className={AppCss.FooterContactDetails}>
                                <h5 className="pt-3 pb-2">Contact Details</h5>
                                <Row>
                                    <Col xs="1">
                                        <FontAwesomeIcon icon={faMapMarker} />
                                    </Col>
                                    <Col>
                                        <small><b>Address</b></small><br />
                                        <small>123, Street , Seattle, WA 98112.</small>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col xs="1">
                                        <FontAwesomeIcon icon={faPhone} />
                                    </Col>
                                    <Col>
                                        <small><b>Call Us</b></small><br />
                                        <small>+123-456-789</small>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col xs="1">
                                        <FontAwesomeIcon icon={faEnvelope} />
                                    </Col>
                                    <Col>
                                        <small><b>Email</b></small><br />
                                        <small>info@companyname.com</small>
                                    </Col>
                                </Row>
                            </Col>

                            <Col md="6" className={AppCss.footerLink}>
                                <h5 className="pt-3 pb-2">Useful Links</h5>
                                <p><small><NavLink to="/frequently-asked-questions" activeClassName={AppCss.active}>Frequently Asked Questions</NavLink></small></p>
                                <p><small><NavLink to="/privacy-policy" activeClassName={AppCss.active}>Privacy Policy</NavLink></small></p>
                                <p><small><NavLink to="/resources" activeClassName={AppCss.active}>Resources</NavLink></small></p>
                            </Col>
                        </Row>
                        <hr />
                        <Row>
                            <Col>
                                <div className="text-center">
                                    <small> &copy; {new Date().getFullYear()} Copyright : All rights reserved</small>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </footer>
            </Router>

        </Fragment >
    );
}

export default App;