import React, { Fragment } from 'react';
import Content from './Content';
import Form from 'react-bootstrap/Form';
import classname from 'classnames';


class FormHandling extends React.Component {
    constructor(props) {
        super(props);
        this.state = { route: '', setError: '' };
        this.handleOnChange = this.handleOnChange.bind(this);
        this.handleOnSubmit = this.handleOnSubmit.bind(this);
    }

    handleOnChange(event) {
        const name = event.target.name;
        const value = event.target.value;
        this.setState({ [name]: value, setError: '' });
    }

    handleOnSubmit(event) {
        let pathToRedirect;
        pathToRedirect = this.state.route.split(" ").join('-');

        if (this.state.route === undefined || this.state.route === "") {
            this.setState({ setError: 'You have to enter route' })
        } else {
            if (pathToRedirect === 'home') {
                this.props.history.push('/')
            } else {
                this.props.history.push('/' + pathToRedirect)
            }
        }
        event.preventDefault();
    }

    render() {
        return (
            <Fragment>
                <Content>Form</Content>
                <Form onSubmit={this.handleOnSubmit}>
                    <Form.Control name='route' value={this.state.firstName} onChange={this.handleOnChange} placeholder="Redirect to Route" className={classname('w-25', 'mb-3')} />
                    <label className={'text-danger'}>{this.state.setError}</label><br />
                    <button type="submit" className={classname('mt-2','btn btn-primary')} >Submit</button>
                </Form>
            </Fragment>
        );
    }
}

export default FormHandling;